import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass'
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass'
import { FXAAShader  } from 'three/examples/jsm/shaders/FXAAShader'
import { RoundedBoxGeometry } from 'three/examples/jsm/geometries/RoundedBoxGeometry'
// import { ClearPass } from 'three/examples/jsm/postprocessing/ClearPass'
// import FresnelShader from './FresnelShader'
import { loadShader } from './ShaderLoader'
import { Stats } from 'three-stats'
import { ShaderMaterial } from 'three'

class RefractionObject {
    mesh: THREE.Mesh
    originalMaterial: THREE.Material | THREE.Material[]
    refractionMaterial: THREE.ShaderMaterial
    // suResolution: THREE.Vector2
    // suBackground: THREE.Texture
    // suDepth: THREE.DepthTexture
    constructor(mesh: THREE.Mesh, suResolution: THREE.Vector2, suBackground: THREE.Texture, suDepth: THREE.DepthTexture,
        absorbColor: THREE.Color, suRefractionFactor: number) {
        this.mesh = mesh
        // copy material
        if (this.mesh.material instanceof Array) {
            this.originalMaterial = new Array<THREE.Material>()
            for (let i = 0; i < this.mesh.material.length; i++) {
                this.originalMaterial.push(this.mesh.material[i].clone())
            }
        }
        else {
            this.originalMaterial = this.mesh.material.clone()
        }
        var refractionMaterialUniforms = {
            "resolution": { type: "v", value: suResolution },
            "tBackground":    { type: "t", value: suBackground },
            "tBackDepth": { type: "t", value: suDepth },
            "refractionFactor": { type: "t", value: suRefractionFactor },
            "absorbColor": { type: "v", value: absorbColor }
        }
        var [glassFrontVS, glassFrontFS] = loadShader('GlassFrontShader')
        this.refractionMaterial = new ShaderMaterial({
            vertexShader:   glassFrontVS,
            fragmentShader: glassFrontFS,
            uniforms: refractionMaterialUniforms,
            // depthTest: false,
            // stencilWrite: true,
            // stencilRef: 999,
            // stencilZPass: THREE.ReplaceStencilOp,  // write if the depth buffer test passes
            side: THREE.FrontSide
        })
    }
}

class RefractionManager {
    renderer: THREE.WebGLRenderer

    frontScene: THREE.Scene
    scene: THREE.Scene

    backdepthTexture: THREE.DepthTexture
    backRenderTarget: THREE.WebGLRenderTarget

    refractionObjectList: Array<RefractionObject>

    suResolution: THREE.Vector2

    constructor(
        scene: THREE.Scene, renderer: THREE.WebGLRenderer,
        backDepthTextureSize: number = 512
    ) {
        this.scene = scene
        this.frontScene = new THREE.Scene()
        this.renderer = renderer
        this.backdepthTexture = new THREE.DepthTexture(
            backDepthTextureSize, backDepthTextureSize
        )
        this.backRenderTarget = new THREE.WebGLRenderTarget(
            container.offsetWidth * window.devicePixelRatio,
            container.offsetHeight * window.devicePixelRatio
        )
        this.backRenderTarget.depthBuffer = true;
        this.backRenderTarget.depthTexture = this.backdepthTexture;
        this.backRenderTarget.depthTexture.type = THREE.UnsignedIntType;
        this.frontScene.background = this.backRenderTarget.texture
        this.refractionObjectList = []
        this.suResolution = new THREE.Vector2(
            container.offsetWidth * window.devicePixelRatio,
            container.offsetHeight * window.devicePixelRatio
        )
    }

    addRefractionMesh(mesh: THREE.Mesh, setDefaultBacksideMaterial: boolean = true,
        absorbColor: THREE.Color = new THREE.Color(0x000000), refractionFactor: number = 1.49) {
        if (setDefaultBacksideMaterial) {
            mesh.material = new THREE.MeshPhysicalMaterial({
                // color: 0x00000000,
                color: '0x000000',
                transparent: true,
                opacity: 0.3,
                metalness: 0.8,
                roughness: 0.0,
                clearcoat: 0.2,
                side: THREE.BackSide
                // colorWrite: false
            })
        }
        this.refractionObjectList.push(new RefractionObject(
            mesh,
            this.suResolution,
            this.backRenderTarget.texture,
            this.backRenderTarget.depthTexture,
            absorbColor,
            refractionFactor
        ))
        this.scene.add(mesh)
    }

    onWindowResize() {
        this.suResolution.x = container.offsetWidth * window.devicePixelRatio
        this.suResolution.y = container.offsetHeight * window.devicePixelRatio
    }

    render() {
        this.refractionObjectList.forEach(refObj => {
            refObj.mesh.material = refObj.originalMaterial
            this.scene.add(refObj.mesh)
        })
        this.renderer.setRenderTarget(this.backRenderTarget)
        this.renderer.render(this.scene, camera)
        
        this.refractionObjectList.forEach(refObj => {
            refObj.mesh.material = refObj.refractionMaterial
            this.frontScene.add(refObj.mesh)
        })
        this.renderer.setRenderTarget(null)
        this.renderer.render(this.frontScene, camera)
    }
}

var container: HTMLElement
var renderer: THREE.WebGLRenderer, camera: THREE.PerspectiveCamera, controls: OrbitControls
var stats: Stats
var scene: THREE.Scene
var rm: RefractionManager

export function initScene(_container: HTMLElement) {
    container = _container
    camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 5000 )
    camera.position.set(30, 20, 200)
    camera.lookAt(0, 0, 0)

    scene = new THREE.Scene()

    renderer = new THREE.WebGLRenderer( {antialias: true, alpha: true} )
    
    renderer.setClearColor( 0xffffff )
    renderer.setPixelRatio( window.devicePixelRatio )
    renderer.setSize(window.innerWidth, window.innerHeight)
    container.appendChild( renderer.domElement )
    controls = new OrbitControls(camera, renderer.domElement)
    renderer.domElement.style.position = 'absolute'
    renderer.domElement.style.top = '0px'
    renderer.domElement.style.left = '0px'
    stats = new Stats()
    stats.domElement.style.zIndex = '100'
    container.appendChild( stats.domElement )

    // set backgroun
    const r = './cube/'

    const urls = [
        r + 'px.jpg', r + 'nx.jpg',
        r + 'py.jpg', r + 'ny.jpg',
        r + 'pz.jpg', r + 'nz.jpg'
    ]

    const textureCube = new THREE.CubeTextureLoader().load( urls )
    textureCube.mapping = THREE.CubeRefractionMapping

    scene.background = textureCube
    scene.environment = textureCube
    
    // set light
    var light = new THREE.DirectionalLight()
    light.position.x = 300
    light.position.y = 200
    light.position.z = 100
    scene.add(light)

    // set floor
    var floorTexture = new THREE.TextureLoader().load( './checkerboard.jpg' )
	floorTexture.wrapS = floorTexture.wrapT = THREE.RepeatWrapping
	floorTexture.repeat.set( 10, 10 )
    var floorMaterial = new THREE.MeshBasicMaterial( { map: floorTexture, side: THREE.DoubleSide } )
	var floorGeometry = new THREE.PlaneGeometry(1000, 1000, 1, 1)
	var floor = new THREE.Mesh(floorGeometry, floorMaterial)
	floor.position.y -= 20.1
	floor.rotation.x = Math.PI / 2
    // scene.add(floor)

    rm = new RefractionManager(scene, renderer)

    var glassMesh1 = new THREE.Mesh(
        new RoundedBoxGeometry(40, 40, 40, 5, 5),
        new THREE.MeshPhongMaterial({})
    )
    glassMesh1.position.x -= 40
    var glassMesh2 = new THREE.Mesh(
        new THREE.SphereGeometry( 20 ),
        new THREE.MeshPhongMaterial({})
    )
    var glassMesh3 = new THREE.Mesh(
        new THREE.CylinderGeometry( 20, 20, 80, 36 ),
        new THREE.MeshPhongMaterial({})
    )
    glassMesh3.position.x += 40
    glassMesh3.position.y += 20

    rm.addRefractionMesh(glassMesh1, true, new THREE.Color(0x000000), 1.5)
    rm.addRefractionMesh(glassMesh2, true, new THREE.Color(0x006000), 1.5)
    rm.addRefractionMesh(glassMesh3, true, new THREE.Color(0x000633), 1.5)

    // add a few spheres
    var ballMesh = new THREE.Mesh(
        new THREE.SphereGeometry( 20 ),
        new THREE.MeshPhongMaterial({
            color: 0xff0000
        })
    )
    ballMesh.position.copy(glassMesh1.position)
    ballMesh.position.z -= 50
    scene.add(ballMesh)

    window.addEventListener( 'resize', onWindowResize )
    animate()

}

function onWindowResize() {

    camera.aspect = window.innerWidth / window.innerHeight
    camera.updateProjectionMatrix()
    rm.onWindowResize()
    renderer.setSize( window.innerWidth, window.innerHeight )

}

function animate() {
    requestAnimationFrame( animate )
    render()
    controls.update()
    stats.update()
}

function render()
{
    rm.render()
}